<template>
  <div class="party">
    <v-app-bar dense flat dark>
      <v-text-field
        :append-icon-cb="() => {}"
        placeholder="Search member..."
        single-line
        append-icon="mdi-account-search"
        color="white"
        hide-details
        v-model="search"
      />
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-container fluid>
      <v-row dense>
        <v-col v-for="(party, i) in filterMembers" :key="i">
          <v-card class="mx-auto" max-width="300">
            <v-toolbar dark dense>
              <v-toolbar-title class="grey--text">{{
                party.party_name
              }}</v-toolbar-title></v-toolbar
            >
            <v-card-text class="orange--text">
              Role : {{ party.party_role }}
            </v-card-text>
            <v-list dense>
              <v-list-item v-for="(member, i) in party.party_members" :key="i">
                <v-list-item-icon>
                  <v-icon>mdi-account</v-icon>
                </v-list-item-icon>
                <v-list-item-title
                  v-text="member.member_name"
                ></v-list-item-title>
              </v-list-item>
            </v-list>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/firestore";

// Get a Firestore instance
export const db = firebase
  .initializeApp({ projectId: "raven-12e4e" })
  .firestore();

export default {
  components: {},
  name: "Party",
  data: () => ({
    parties: [],
    search: ""
  }),
  methods: {
    partyListData() {
      return {
        props: {
          dense: true
        }
      };
    }
  },
  firestore: {
    parties: db.collection("party")
  },
  computed: {
    filterMembers: function() {
      let filtered = this.parties;
      let search = this.search.toLowerCase();
      if (search) {
        filtered = filtered.filter(r => {
          let members = r.party_members.filter(i => {
            return i.member_name.toLowerCase().indexOf(search) > -1;
          });
          return members.length > 0;
        });
      }
      return filtered;
    }
  }
};
</script>
